.footer {
  background-color: #25201d;
  color: #ffffff;
}

.footer-div {
  width: 80%;
  margin: 0 auto;
}

.footer-text {
  width: 400px;
}

.footer-text h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  letter-spacing: -0.03em;
}

.footer-text p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.02em;
}

.footer-img {
  width: 300px;
  height: 300px;
}

.footer-img img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: -25%;
  top: -10%;
}

.footer-top-div {
  width: 90%;
  margin: 0 auto;
  position: relative;
}

.footer-form {
  color: #ffffff;
  position: relative;
}

.footer-form-input {
  background-color: #494949;
  padding: 22px;
}

.footer-btn {
  position: absolute;
  left: 71%;
  top: 9%;
  background-color: #ffffff !important;
  color: #494949 !important;
}

@media screen and (max-width: 426px) {
  .footer-img {
    display: none;
  }

  .footer-text {
    display: flex;
    flex-direction: column;
  }

  .footer-div {
    width: 100%;
  }

  .footer-text {
    width: 400px;
    margin: 40px 0 40px 0;
  }

  .footer-text h1 {
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 40px;
    letter-spacing: -0.03em;
  }

  .footer-text p {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }

  .footer-btn {
    position: absolute;
    left: 70%;
    top: 9%;
    background-color: #ffffff !important;
    color: #494949 !important;
  }

  .footer-form {
    margin-top: 20px;
  }
}
