.about {
  width: 80%;
  margin: 0 auto;
}

.about-intro {
  width: 586px;
}

.about-intro h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: -0.02em;
}

.about-intro p {
  width: 500px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 31px;
  letter-spacing: 0.02em;
  color: #494949;
}

.experience {
  background-color: #222222;
  color: #ffffff;
}

.experience-div {
  width: 80%;
  margin: 0px auto;
  padding-top: 40px;
}

.experience-article {
  width: 450px;
}

.experience-article h4 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 39px;
  letter-spacing: 0.01em;
}

.experience-article p {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
}

.experience-badge {
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 4px;
  font-weight: 100;
}

.customer-reviews {
  width: 80%;
  margin: 60px auto;
  background-color: #ffffff;
  z-index: 1000 !important;
}

.vendor {
  background-color: #fff8f5;
}

.vendor-div {
  width: 80%;
  padding-top: 80px;
  margin: 0 auto;
}

.vendor-badge {
  border: 1px solid rgba(255, 92, 0, 0.4);
  color: #ff5c00;
  font-weight: 500;
}

.vendor-share {
  color: #ff5c00;
  font-size: 16px;
}

.vendor-image {
  position: relative;
}

.ellipse {
  position: absolute;
  right: 0;
}

.share {
  cursor: pointer;
}

@media screen and (max-width: 426px) {
  .about {
    display: flex;
    flex-direction: column;
  }

  .about-intro {
    width: 300px;
  }

  .about-intro h1 {
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    /* letter-spacing: -0.02em; */
  }

  .about-intro p {
    width: 300px;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 31px;
    letter-spacing: 0.02em;
    color: #494949;
  }

  .about img {
    width: 300px;
  }

  .experience img {
    display: none;
  }

  .vendor-div {
    display: flex;
    flex-direction: column;
  }

  .experience-article {
    width: 100%;
    margin-bottom: 40px;
  }

  .experience-article h4 {
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 39px;
    letter-spacing: 0.01em;
  }

  .experience-article p {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.02em;
  }

  .vendor-image img {
    width: 300px;
    margin-top: 40px;
  }
}
